import React, { useLayoutEffect, useState } from 'react';
import { Link, useNavigate, useParams } from 'react-router-dom';
import { fnDeleteDoc, fnGetDoc, fnUpdateDoc } from '../fb/db';
import { auth } from '../fb/auth';

const CompDetail = () => {
  const { docid } = useParams()

  const [_title, _setTitle] = useState('')
  const [_date, _setDate] = useState('')
  const [_desc, _setDesc] = useState('')

  const fnGetDocHandler = async () => {
    const doc = await fnGetDoc(auth.currentUser.uid, docid)
    _setDate(doc.date)
    _setTitle(doc.title)
    _setDesc(doc.desc)
  }
  const navi = useNavigate()
  useLayoutEffect(() => {
    fnGetDocHandler()
  }, [])

  const fnUpdateDocHandler = async (e) => {
    e.preventDefault()
    const result = window.confirm(' ?')
    if (!result) return
    const data = {
      title: _title,
      date: _date,
      desc: _desc,
    }
    await fnUpdateDoc(auth.currentUser.uid, docid, data)
    alert('  .  ')
    navi('/')
  }

  const fnDeleteDocHandler = async (e) => {
    e.preventDefault()
    const result = window.confirm(' ?')
    if (!result) return
    await fnDeleteDoc(auth.currentUser.uid, docid)
    alert('  .  ')
    navi('/')
  }

  return (
    <section>
      <h3></h3>
      <form>
        <p> <input onChange={(e) => { _setDate(e.target.value) }} value={_date} type="date" /></p>
        <p> <input onChange={(e) => { _setTitle(e.target.value) }} value={_title} type="text" /></p>
        <p> <textarea onChange={(e) => { _setDesc(e.target.value) }} value={_desc}></textarea></p>
        <p>
          <button onClick={fnUpdateDocHandler}></button>
          <button onClick={fnDeleteDocHandler}></button>
          <Link to='/'></Link>
        </p>

      </form>
    </section>
  );
};

export default CompDetail;