import React, { useContext, useState } from 'react';
import { auth, fnCheckEmailVerification, fnSetPersistence, fnSignIn, fnSignInGoogle, fnSignOut } from '../fb/auth';
import { Appcontext } from '../App';


const CompSignIn = () => {
  const { navi } = useContext(Appcontext)
  const [_email, _setEmail] = useState('')
  const [_password, _setPassword] = useState('')
  const [_checked, _setChecked] = useState(false)

  const fnSignInHandler = async (e) => {
    e.preventDefault()
    await fnSetPersistence(_checked)
    await fnSignIn(_email, _password)
    if (fnCheckEmailVerification()) {
      alert(`${auth.currentUser.displayName} `)
      navi('/')
    } else {
      await fnSignOut()
      _setEmail('')
      _setPassword('')
      alert('    ')
    }
  }

  const fnSignInGoogleHandler = async () => {
    await fnSignInGoogle()
    alert('   ')
    navi('/')
  }

  return (
    <section>
      <h3></h3>
      <form onSubmit={fnSignInHandler}>
        <p> : <input value={_email} onChange={(e) => { _setEmail(e.target.value) }} type="email" required /></p>
        <p> : <input value={_password} onChange={(e) => { _setPassword(e.target.value) }} type="password" required /></p>
        <button></button>
        <p><input type="checkbox" checked={_checked} onChange={() => { _setChecked(c => !c) }} /> </p>

      </form>
      <button onClick={fnSignInGoogleHandler}></button>
    </section>
  );
};

export default CompSignIn;